

























































































	// 引入icon
	import UniIcon from '@/components/uni-icon.vue'
	export default {
		components:{
			UniIcon
		},
		data() {
			return {
				imgs:'',
				data:[1,2,3,4,5,6],
				userName:'',
				userId:'',
				mymoney:0,
				yourf:0,
				userImg:'',
				defualtImg:'../../static/girl.png',
				iconimg:false,
				userInfo:"",
				mess:false,
				parentid:''
			}
		},
		onLoad() {
			let that=this;
		uni.getSetting({
		   success(res) {
			  console.log(res.authSetting)
		   }
		})
		this.parentid=uni.getStorageSync('partId');
		},
		methods: {
			mymoneys(){
				let open=uni.getStorageSync('weixiOpenId')
				uni.request({
					url: 'http://appserver.wujie520.cn/thirdreturn/index/wxintegral?openid='+open,
					method: 'GET',
					data: {},
					success: res => {
						this.mymoney=res.data.data.integral;	
						console.log(this.mymoney)				
					}
				});
			},
			feshi(){
				let open=uni.getStorageSync('weixiOpenId')
				uni.request({
					url: 'http://appserver.wujie520.cn/thirdreturn/index/wxfans?openid='+open,
					method: 'GET',
					data: {},
					success: res => {
						this.yourf=res.data.data.fans_count;
					}
				});
			},
			mpGetUserInfo(result) {
				let that=this;
				console.log('mpGetUserInfo', result);
				this.userName=result.mp.detail.userInfo.nickName;
				let userimgUrl=result.mp.detail.userInfo.avatarUrl;
				uni.setStorageSync('userimgUrl',userimgUrl)
				this.userImg=uni.getStorageSync('userimgUrl');
				uni.setStorageSync('userName',result.mp.detail.userInfo.nickName);
				if (result.detail.errMsg !== 'getUserInfo:ok') {
					uni.showModal({
						title: '获取用户信息失败',
						content: '错误原因' + result.detail.errMsg,
						showCancel: false
					});
					return;
				}else{
					uni.login({
					  provider: 'weixin',
					  success: function (loginRes) {
						console.log(loginRes);
						let weixiCode=loginRes.code;
						uni.setStorageSync('weixiCode',weixiCode);
						uni.request({
							url: 'http://appserver.wujie520.cn/thirdreturn/index/getopenid?code='+weixiCode,
							method: 'GET',
							success: res => {
								console.log(JSON.parse(res.data));
								let weixiOpenId=JSON.parse(res.data).openid;
								uni.setStorageSync('weixiOpenId',weixiOpenId);
								console.log('获取到openid',weixiOpenId);
								let name=uni.getStorageSync('userName');
								console.log("获取到userName",name);
								var parentid=uni.getStorageSync('partId');
								console.log("获取到partId",parentid);
								uni.request({
									url: 'http://appserver.wujie520.cn/thirdreturn/index/wxlogin?openid='+weixiOpenId+'&name='+name+'&parentid='+that.parentid,
									method: 'GET',
									success: res => {
										console.log("返回的数据:",res.data);
										uni.setStorageSync("userId",res.data.data.user_id);
										that.userId=res.data.data.user_id;
										uni.setStorageSync("parentId",res.data.data.parent_id);
										that.mymoneys();
										that.feshi();
									}
								});
							}
						});
					  }
					});
				}
				this.hasUserInfo = true;
				this.userInfo = result.detail.userInfo;
				console.log(this.userInfo)
			},
			// 清除数据
			cleardata(){
				uni.showModal({
				title: '温馨提示',
				content: '你确定要清除数据吗',
				showCancel: true,
				success: function (res) {
					if (res.confirm) {
						uni.clearStorage()
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
                   }
			});
			},
			// 订单
			ordermess(){
				uni.navigateTo({
					url: '../ordermess/ordermess?id=15'
				});
			},
			// 添加用户
			adduser(){
				
			}
			
		},
		destroyed() {
			
		},
		onShow() {
			this.mess=true;
			this.mymoneys();
			this.feshi();
			this.userId=uni.getStorageSync('userId');
			this.userName=uni.getStorageSync('userName');
			this.userImg=uni.getStorageSync('userimgUrl');
			// 获取partid
			this.parentid=uni.getStorageSync('partId');
		},
		onTabItemTap(){
			this.mymoneys();
			this.feshi();
		}
	}
